import React, {PureComponent} from 'react'
import {Form, Button, Row, Col, message, Input, Select} from 'antd'
import style from './style/Form.scss'
import {CsxSelect, CsxMultipleSelect, CsxMultipleSearch, CsxSearch} from 'Form'
import {connect} from 'react-redux'
import {proTypeCatorageList} from 'Mock'
import {getData} from 'util'
import {API_PRODUCTS_KEYVALUES, API_USER_WAREHOUSELIST} from 'api'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {debounce} from 'lodash'

@connect(
  (state) => ({
    ...state.reservationInwarehouse,
    userLoation: state.userLoation
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      searchProList: [] //商品列表
    }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }

  //查询
  query = debounce(() => {
    const {handleGetList, searchParams = {}, size} = this.props
    const {categoryCodes = '', productType = ''} = searchParams
    console.log('searchParams is', searchParams)
    if (categoryCodes && !(productType && productType.value)) {
      return message.error('请选择商品类型')
    }
    handleGetList({searchParams: {...searchParams, page: 1, size}})
  }, 600)

  // 重置
  reset = () => {
    const {update, handleGetList, size} = this.props
    update({searchParams: {}})
    handleGetList({searchParams: {page: 1, size}})
  }

  render() {
    const {
      searchParams: {
        sapCusCode = [],
        productCode1 = [],
        warehouseCode,
        sapSubCusCode = '',
        pickingAreaCode = []
      },
      userLoation,
      modalTuttonLoading
    } = this.props
    const {searchProList = []} = this.state
    return (
      <Form className={style.form}>
        <Row type="flex" justify="start">
          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_USER_WAREHOUSELIST}
              label="仓库"
              selectItem={{
                data: warehouseCode,
                key: 'warehouseCode',
                valueKey: ['warehouseCode', 'warehouseName']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Row type="flex" justify="end">
              <Button
                type="primary"
                style={{marginRight: 20}}
                loading={modalTuttonLoading}
                onClick={this.query}
              >
                查询
              </Button>
              <Button type="primary" onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



